import React from "react";
import { DataTable, Text } from "grommet";
import { Close, Configure } from "grommet-icons";
const Component = ({ data, onConfigure, onDelete }) => {
  return (
    <>
      <DataTable
        columns={[
          {
            property: "name",
            header: <Text>Name</Text>,
            primary: true,
          },
          {
            property: "description",
            header: <Text>Description</Text>,
          },
          {
            property: "startBid",
            header: <Text>Start Bid</Text>,
          },
          {
            property: "instantClosePrice",
            header: <Text>Instant Close Price</Text>,
          },
          {
            property: "Status",
            header: <Text>Status</Text>,
          },
          {
            property: "available",
            header: <Text>Display</Text>,
          },
          {
            property: "",
            header: <Text>Action</Text>,
            render: (datum) => (
              <>
                <Configure
                  onClick={() => onConfigure && onConfigure(datum)}
                  style={{ cursor: "pointer" }}
                />
                <Close
                  onClick={() => onDelete && onDelete(datum)}
                  style={{ cursor: "pointer" }}
                />
              </>
            ),
          },
        ]}
        data={data}
      />
    </>
  );
};

export default Component;
