import React, { useEffect, useState } from "react";
import { Box, TextInput, Anchor } from "grommet";
import ProductGrid from "../../components/DataGrid";
import ProductForm from "../../components/Forms/Product";
import Modal from "../../components/Modal";
import {
  findMyStore,
  findProduct,
  deleteOneProduct,
  updateOneProduct,
  createOneProduct,
} from "../../library/parse";
import { cloneJSONKey } from "../../helpers";
import Parse from "../../library/parse";

const App = ({ id }) => {
  const Store = Parse && Parse.Object && Parse.Object.extend("Store");
  const [formModal, setformModal] = useState({});
  const [activeStore] = useState(Store && new Store());
  const [editModal, setEditModal] = useState({});
  const [showModal, setShowModal] = useState(false);
  const [store, setStore] = useState(null);
  const [myProducts, setMyProducts] = useState([]);
  const [productFormMode, setProductFormMode] = useState("create");

  useEffect(() => {
    const run = async () => {
      const stores = await findMyStore();
      setStore(stores.find((e) => e.id === id));
    };
    run();
  }, [id]);

  useEffect(() => {
    if (store) {
      setEditModal({
        name: store.get("name"),
      });
      const loadProduct = async () => {
        setMyProducts(await findProduct(store));
      };
      loadProduct();
    }
  }, [store]);
  return (
    <>
      <Box fill={true} justify="center" align="center" alignContent="center">
        {
          <>
            <Box fill={true} gap="small" direction="row">
              <TextInput
                plain={false}
                placeholder="choose a store name"
                value={editModal.name}
                onChange={(event) => {
                  setEditModal({
                    ...editModal,
                    objectId: activeStore && activeStore.id,
                    name: event.target.value,
                  });
                }}
              />
              <Anchor onClick={() => setShowModal(true)}>
                Change Store Name
              </Anchor>
              <Anchor
                onClick={() => {
                  setProductFormMode("create");
                  setformModal({});
                  setShowModal(true);
                }}
              >
                Add Product
              </Anchor>
            </Box>
            <Box fill={true}>
              <ProductGrid
                data={myProducts ? myProducts.map((e) => e.toJSON()) : []}
                onConfigure={(modal) => {
                  setProductFormMode("update");
                  setformModal(modal);
                  setShowModal(true);
                }}
                onDelete={(modal) => {
                  setProductFormMode("delete");
                  setformModal(modal);
                  setShowModal(true);
                }}
              />
            </Box>
          </>
        }
      </Box>
      {showModal && (
        <Modal onClose={() => setShowModal(false)}>
          <ProductForm
            mode={productFormMode}
            formModal={formModal}
            onModal={async (action, modal) => {
              const keyToSend = [
                "name",
                "photo",
                "startBid",
                "instantClosePrice",
                "objectId",
                "available",
              ];
              const storeId = store.id;
              modal = cloneJSONKey(modal, keyToSend);

              if (action === "create") {
                await createOneProduct(modal, store.id);
              }
              if (action === "update") {
                await updateOneProduct({
                  ...modal,
                  productId: modal.objectId,
                  storeId,
                });
              }
              if (action === "delete") {
                await deleteOneProduct({
                  productId: modal.objectId,
                  storeId,
                });
              }
              setTimeout(
                () =>
                  findProduct(store).then((r) => {
                    setMyProducts(r);
                    setShowModal(false);
                  }),
                1000
              );
            }}
            onClose={() => setShowModal(false)}
          />
        </Modal>
      )}
    </>
  );
};
export default App;
